// components/reviews/StatCard.tsx
/**
 * **StatCard**
 *
 * Tarjeta con dato destacado + icono y degradado configurable.
 */
export default function StatCard({
  title,
  value,
  IconComponent,
  gradientFrom = "from-yellow-400/60",
  gradientTo   = "to-orange-500/60",
}: {
  title: string;
  value: string;
  IconComponent: React.ComponentType<React.SVGProps<SVGSVGElement>>;
  gradientFrom?: string;
  gradientTo?: string;
}) {
  return (
    <div className={`relative bg-gradient-to-br ${gradientFrom} ${gradientTo} backdrop-blur-lg p-4 rounded-lg shadow-lg transition-transform hover:scale-105 hover:shadow-xl`}>
      <div className="absolute inset-0 bg-black/15" />

      <div className="relative flex items-center space-x-4">
        <div className={`w-14 p-2 rounded-full bg-gradient-to-r ${gradientFrom} ${gradientTo} shadow-2xl`}>
          <IconComponent className="w-10 h-10 text-white" />
        </div>

        <div>
          <span className="text-2xl text-white/90 font-bold">{value}</span>
          <p className="text-sm text-gray-100">{title}</p>
        </div>
      </div>
    </div>
  );
}
